import React, { useState } from "react";

const TextForm = (props) => {
  const handelUpClick = () => {
    // console.log("handle was click" + text);
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert('Uppercase as been completed','success');
  };
  const handelLoClick = () => {
    // console.log("handle was click" + text);
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert('Lowercase as been completed','success');
  };
  const handleOnChange = (event) => {
    // console.log("handleonchange was click");
    setText(event.target.value);
  };
  const clear = (event) => {
    // console.log("handleonchange was click");
    setText("");
    props.showAlert('TextArea is clear','success');
  };

  const handleCopy = () => {
    var text = document.getElementById('myBox');
    text.select();
    navigator.clipboard.writeText(text.value);
    props.showAlert('Text Copied Successful','success');
    }

    const handleExtraSpaces = () => {
      let newText = text.split(/[ ]+/);
      setText(newText.join(' '))
      props.showAlert('Extra spaces has been removed','success');
      }
    

  const [text, setText] = useState("Enter text here");
  return (
    <>
      <div className="container" style={{color: props.mode === 'dark'?'white':'black'}}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea
            className="form-control"
            value={text}
            onChange={handleOnChange}
            id="myBox"
            style={{backgroundColor: props.mode === 'dark'?'#222':'white',color: props.mode ==='dark'?'white':'black'}}
            rows="8"
          ></textarea>
        </div>

        <button className="btn btn-primary mx-3" onClick={handelUpClick}>
          Convert to Uppercase
        </button>
        <button className="btn btn-primary mx-3" onClick={handelLoClick}>
          Convert to Lowercase
        </button>
        <button className="btn btn-primary" onClick={clear}>
          Clear Text
        </button>
        <button className="btn btn-primary mx-3" onClick={handleCopy}>
          Copy Text
        </button>
        <button className="btn btn-primary mx-3" onClick={handleExtraSpaces}>
         Remove Extra spaces
        </button>
      </div>

      <div className="container my-3" style={{color: props.mode ==='dark'?'white':'black'}}>
        <h2>Your Text Summary</h2>
        <p>
          {text.split(" ").length} words and {text.split(" ").join("").length}{" "}
          characters
        </p>
        <p>{0.008 * text.split("").length} Minutes to read</p>
        <h3>Preview</h3>
        <p>{text.length>0 ? text : "Enter the text to see a preview"}</p>
      </div>
    </>
  );
};

export default TextForm;
